input {
    font-size: 16px;
    background-color: rgba(0, 0, 0, 0.1);
    border-radius: 3px;
    box-sizing: border-box;
    border: 1px solid rgba(0, 0, 0, 0.3);
    color: #dcddde;
    padding: 10px;
}

input:hover {
    border-color: #040405;
}

input label {
    margin-bottom: 3px;
    padding: 0;
    display: block;
    font-weight: bold;
}

input:focus {
    border-color: #7289da;
}

input[type='number'] {
    -moz-appearance:textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

input span {
    color: #f01f30;
    font-size: 12px;
    font-weight: bold;
}

input.invalid label {
    color: #f01f30;
}